body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app {
  padding: 70px 0 25px;
  background: #e9e9e9;
  background-image: -webkit-linear-gradient(45deg, rgba(255, 255, 255, 0.4) 25%, transparent 25%, transparent 50%, rgba(255, 255, 255, 0.4) 50%, rgba(255, 255, 255, .4) 75%, transparent 75%, transparent);
  background-image: linear-gradient(45deg, rgba(255, 255, 255, 0.4) 25%, transparent 25%, transparent 50%, rgba(255, 255, 255, 0.4) 50%, rgba(255, 255, 255, .4) 75%, transparent 75%, transparent);
  background-size: 5px 5px;
  min-height: 94vh;
  overflow: hidden;
}
.main {
  z-index: 1;
  position: relative;
  margin-top: 20px;
  width: auto;
  height: auto;
  background-color: rgba(212, 212, 212, 0.7);
}
.main .card {
  padding-left: 40px;
}
.main .topic{
  color: rgb(51, 51, 51);
}
.main .infocard {
  margin: 10px 10px 10px 10px;
  background-color: white;
  border-style: solid 10px;
  border-color: gray;
}
.main .emptyinfocard {
  margin: 10px 10px 10px 10px;
  background-color: white;
  border-style: solid 10px;
  border-color: gray;
  text-align: center;
  padding-top: 10px;
}
.main .empty {
  height: 0px;
}
.main h1:hover {
  color: #E7405A;
}
.main a {
  text-decoration: none;
}
.card h1:first-letter {
  text-transform:uppercase
}
#number{
  margin-right: 40px;
  width: 50px;
  height: 50px;
}
.infocard h1 {
  font-size: 20px;
}
.VCcard {margin-top: 20px;}
.VCcard strong {font-size: 20px;}

.footer {
  position: bottom;
  background-color: black;
  width: auto;
  height: 40px;
}
.footer p{
  color: white;
  padding-top: 10px;
  padding-left: 15px;
}
.likeicons {
  font-size: 20px;
  display: inline-block;
}
.likeicons .glyphicon-ok:hover{
  cursor: pointer;
  color: #52b4ff;
}
.likeicons .glyphicon-ok .clicked{
  color: #52b4ff;
}
.likeicons .glyphicon-remove:hover{
  cursor: pointer;
  color: red;
}
.likeicons .glyphicon-remove .clicked {
  color: red;
}
.docfile {
  font-size: 50px;
  margin-top: 25px;
  color: gray;
}
.hidden {
  display:none
}
.secWorkNavbar {
  z-index: 10;
  margin-top: 70px;
  width: auto;
  height: 90px;
  background-color: rgba(0, 0, 0, 0.7);;
  color: rgb(216, 216, 216);
  text-transform: uppercase;
  position: fixed;
}
.infocard .button {
  border-width: 0px;
  border-bottom-width: 1px;
  border-style: solid;
  border-bottom-color: rgb(219, 82, 82);
  width: 100px;
  height: 40px;
  padding-top: 8px;
  text-align: center;
  margin-bottom: 15px;
}
.infocard .button:hover {
  border-style: solid;
  border-bottom-color: rgb(219, 82, 82);
  width: 100px;
  height: 40px;
  padding-top: 8px;
  text-align: center;
  margin-bottom: 15px;
  cursor: pointer; 
  box-shadow: 0 4px 8px 3px rgba(0, 0, 0, 0.144), 0 6px 20px 3px rgba(0, 0, 0, 0.13);
  -webkit-transition: all 300ms ease-out;
  transition: all 300ms ease-out;
}

li a {cursor: pointer;}
