.Navbar {
    background-color: rgba(0, 0, 0, 0.6);
    border-bottom: rgba(0, 0, 0, 0.6);
    width: 100%;
    height: 70px;
    color: rgb(216, 216, 216);
    text-transform: uppercase;
    position: fixed;
    z-index: 2;
}
.Navbar .logonav img {
    padding-top: 10px;
    width: 90px;
    height: 60px;
}
.navitems {
    width: auto;
    height: auto;
    float: right;
}
@media (min-width: 768px) {
    .navitems {    
        margin-right: 100px;
    }
}
.navitems ul {
  list-style-type: none;
  margin: 0;
  padding-top: 10px;
  overflow: hidden;
}

.navitems li {
  float: left;
}

.navitems li a {
  display: block;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

.navitems li a:hover:not(.active) {
  background-color: rgba(17, 17, 17, 0.158);
}

.navitems .active {
  background-color: #4CAF50;
}
@media (max-width: 768px) {
    .Navbar .logonav{
        padding-top: 10px;
        text-align: center;
    }
    .Navbar {
        height: auto;
        display: flex;
        align-items: center;
        justify-content: center;
    }
}
.secWorkNavbar {
  margin-top: 70px;
  /*margin-left: 10%;
  margin-right: 10%;*/
  width: 10000px;
  height: 50px;
  background-color: rgba(90, 173, 155, 0.897);
  color: rgb(255, 255, 255);
  text-transform: uppercase;
  position: fixed;
  margin-bottom: 5px;
}
.baritems {margin-left: 100px;}
@media (max-width: 900px) {
  .baritems {margin-left: 0;}
}
.secWorkNavbar .fa-lightbulb-o {color: yellow; }

.secWorkNavbar .doing {
  margin-left: 20px;  
  padding-top: 15px;
  font-size: 15px;
  height: 90px;
  width: 170px;
}
.secWorkNavbar .fa-chevron-down{
  -webkit-transform: rotate(270deg);
}
.secWorkNavbar .areas {
  padding-left: 0;
  padding-top: 15px;
  font-size: 15px;
  height: auto;
  width: auto;
}
.secWorkNavbar .areas p {
  display: inline-block;
  margin: 0;
  padding-right: 10px;
}

.secWorkNavbar .areas p {
  position: relative;
  color: rgb(255, 255, 255);
  text-decoration: none;
}

.secWorkNavbar .areas p:hover {
  color: rgb(0, 0, 0);
  cursor: pointer;
}

.secWorkNavbar .areas p:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: 0;
  left: 0;
  background: -webkit-linear-gradient(left, rgba(82, 180, 255, 1) 0%, rgba(86, 255, 74, 1) 100%);
  background: linear-gradient(90deg, rgba(82, 180, 255, 1) 0%, rgba(86, 255, 74, 1) 100%);
  visibility: hidden;
  -webkit-transform: scaleX(0);
  transform: scaleX(0);
  -webkit-transition: all 0.3s ease-in-out 0s;
  transition: all 0.3s ease-in-out 0s;
}

.secWorkNavbar .areas p:hover:before {
  visibility: visible;
  -webkit-transform: scaleX(1);
  transform: scaleX(1);
}
.secWorkNavbar .areas .areaselected {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: 0;
  left: 0;
  background: -webkit-linear-gradient(left, rgb(0, 86, 151) 0%, rgba(86, 255, 74, 1) 100%);
  background: linear-gradient(90deg, rgb(0, 86, 151) 0%, rgba(86, 255, 74, 1) 100%);
  visibility: visible;
  -webkit-transform: scaleX(1);
  transform: scaleX(1);
}

.secWorkNavbar .steps {
  padding-top: 15px;
  font-size: 14px;
  height: auto;
  width: auto;
  padding-right: 20px;
}
.secWorkNavbar .steps p {
  margin: 0;
  padding-right: 10px;
  display: inline-block;
}

.secWorkNavbar .steps p {
  position: relative;
  color: rgb(255, 255, 255);
  text-decoration: none;
}

.secWorkNavbar .steps p:hover {
  color: rgb(0, 0, 0);
  cursor: pointer;
}

.secWorkNavbar .steps p:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: 0;
  left: 0;
  background: -webkit-linear-gradient(left, #E7405A 0%, #F28F45 100%);
  background: linear-gradient(90deg, #E7405A 0%, #F28F45 100%); 
  visibility: hidden;
  -webkit-transform: scaleX(0);
  transform: scaleX(0);
  -webkit-transition: all 0.3s ease-in-out 0s;
  transition: all 0.3s ease-in-out 0s;
}

.secWorkNavbar .steps p:hover:before {
  visibility: visible;
  -webkit-transform: scaleX(1);
  transform: scaleX(1);
}
.secWorkNavbar .steps .stepselected {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: 0;
  left: 0;
  background: -webkit-linear-gradient(left, #E7405A 0%, #F28F45 100%);
  background: linear-gradient(90deg, #E7405A 0%, #F28F45 100%);  
  visibility: visible;
  -webkit-transform: scaleX(1);
  transform: scaleX(1);
}
.secWorkNavbar .steps {
  border-width: 0px;
  border-left-width: 1px;
  border-style: solid;
  border-left-color: rgba(0, 100, 100, 0.473)
}
